function stepsForms() {
  const steps = document.querySelectorAll('.steps__forms')
  const backBtn = document.querySelector('.back__step')
  const nextBtn = document.querySelector('.next__step')
  const form = document.querySelector('.steps__form')
  const stepNumbers = document.querySelectorAll('.step__number')
  const progress = document.querySelector('.progress__success')
  const finishBlock = document.querySelector('.finish')

  // відміняю стандартну поведінку браузера
  form.addEventListener('submit', (e) => e.preventDefault())

  let formStepIndex = 0

  // задаю поведінку для кнопок
  backBtn.addEventListener('click', () => {
    formStepIndex--

    stepNumbers[formStepIndex + 1].classList.remove('step__number--active')

    updateFormSteps()
  })
  nextBtn.addEventListener('click', () => {
    if(formStepIndex < steps.length -1){
      formStepIndex++
      updateFormSteps()
    }
  })

  function updateFormSteps() {
    steps.forEach((step) => {
      step.classList.contains('steps__forms--active') && step.classList.remove('steps__forms--active')
    })

    steps[formStepIndex].classList.add('steps__forms--active')
    stepNumbers[formStepIndex].classList.add('step__number--active')

    if(formStepIndex === 0){
      backBtn.style.display = 'none'
    } else{
      backBtn.style.display = 'inline'
    }

    if(formStepIndex === steps.length - 1){
      nextBtn.innerHTML = "Finish"

      nextBtn.addEventListener('click', () => {
        finishBlock.style.display = 'block'
        form.style.display = 'none'
      })
    } else{
      nextBtn.innerHTML = "Next"
    }

    const actives = document.querySelectorAll('.step__number--active')
    const percent = (( actives.length -1) / (stepNumbers.length -1)) * 100 + '%'

    progress.style.width = percent
  }

  updateFormSteps()
}

//ми виизваємо форму
if(document.querySelector('.steps__forms')){
stepsForms()
}